.button {
    margin: 0 5px;
    padding: 5px 5px;
    width: 30px;
    border-radius: 50%;
    background: white;
    position: relative;
    cursor: pointer;
    justify-content: space-evenly;
    align-items: center;
}
.button:hover,
.button:active {
  background-color: rgb(137, 184, 228);
}


